/* .leyd-carousel-certificate{

} */


/* Mobile Devices <= 600px ".s" container 90% */
@media (max-width: 600px) { 
    .leyd-container-carousel-certificate,
    .leyd-slide-carousel-certificate{
        width: 300px !important ;
        height: 200px !important;
    }
}

/* Mobile Devices > 600px ".m" container 85% */
@media (min-width: 601px) and (max-width: 992px) { 
    .leyd-container-carousel-certificate,
    .leyd-slide-carousel-certificate{
        width: 420px !important ;
        height: 300px !important;
    }
}

/* Mobile Devices > 992px ".l" container 70% */
@media (min-width: 993px) and (max-width: 1200px) { 
    .leyd-container-carousel-certificate,
    .leyd-slide-carousel-certificate{
        width: 520px !important ;
        height: 350px !important;
    }
}

/* Mobile Devices > 1200px ".xl" container 70% */
@media (min-width: 1201px) {
    .leyd-container-carousel-certificate,
    .leyd-slide-carousel-certificate{
        width: 550px !important ;
        height: 400px !important;
    }
}