.leyd-carousel{
    width: 100%;
    height: 20rem;
    overflow: auto;
    white-space: nowrap;
    overflow-y: hidden;
}

.leyd-carousel .item{
    height: 100%;
    margin: 0 .5rem;
    width: fit-content;
    display: inline-block;
}

/* Mobile devices */
@media (max-width: 599.98px) { 
    .leyd-carousel{
        width: 100%;
        height: 35rem;
        overflow: auto;
        white-space: nowrap;
        overflow-y: hidden;
    }
}

/* Tablet devices */
@media (min-width: 600px) and (max-width: 991px) { 
    .leyd-carousel{
        width: 100%;
        height: 30rem;
        overflow: auto;
        white-space: nowrap;
        overflow-y: hidden;
    }
}

/* Desktop devices */
@media (min-width: 992px) and (max-width: 1199px) { 
    .leyd-carousel{
        width: 100%;
        height: 40rem;
        overflow: auto;
        white-space: nowrap;
        overflow-y: hidden;
    }
}

/* Large devices */
@media (min-width: 1200px) { 
    .leyd-carousel{
        width: 100%;
        height: 40rem;
        overflow: auto;
        white-space: nowrap;
        overflow-y: hidden;
    }
}
