.leyd-photo-container-whoami,
.leyd-text-container-whoami{
    display: flex;
    justify-content: center;
    align-items: center;
}


.leyd-photo-whoami{
    border-radius: 50%;
    display: block;
    width: 240px;
}

.leyd-text-whoami{
    display: inline-block
}

/* Mobile Devices <= 600px ".s" container 90% */
@media (max-width: 600px) { 
    .leyd-photo-whoami{
        padding-bottom: 1em;
        width: 210px;
    }
}

/* Mobile Devices > 600px ".m" container 85% */
@media (min-width: 601px) and (max-width: 992px) {  
    .leyd-photo-whoami{
        padding-bottom: 1em;
    }
}

