#ModalBoxProject {
    padding: 2rem 0;
}
#ModalBoxProject .button-close{
    position: absolute; 
    right: 0.7rem;
    box-shadow: unset;
    background-color: #039be5;
}
/* Mobile devices */
@media (max-width: 599.98px) { 
    #ModalBoxProject {
        top: 0 !important;
        width: 100% !important;
        height: 100% !important;
        max-height: 100% !important;
    }

    #ModalBoxProject .logo{
        width: 7rem;
    }

    #ModalBoxProject .title{
        font-size: 1.7em;
        margin-top: 1rem;
    }
    #ModalBoxProject .title-stacks{
        margin-bottom: 1rem;
    }
    #ModalBoxProject .description{
        text-align: center;
    }
}

/* Tablet devices */
@media (min-width: 600px) and (max-width: 991px) { 
    #ModalBoxProject {
        width: 95% !important;
        height: 85% !important;
        max-height: 100% !important;
    }
    #ModalBoxProject .logo{
        width: 7rem;
    }
    #ModalBoxProject .title{
        font-size: 1.7em;
        margin-top: 1rem;
    }
}

/* Desktop devices (tablets, 768px and up) */
@media (min-width: 992px) and (max-width: 1199px) { 
    #ModalBoxProject {
        width: 95% !important;
        height: 85% !important;
        max-height: 100% !important;
    }
    #ModalBoxProject .logo{
        width: 7rem;
    }
    #ModalBoxProject .title{
        font-size: 1.7em;
        margin-top: 1rem;
    }

}

/* Large devices (desktops, 992px and up) */
@media (min-width: 1200px) { 
    #ModalBoxProject {
        width: 59% !important;
        height: 88% !important;
        max-height: 100% !important;
    }
    #ModalBoxProject .logo{
        width: 7rem;
    }
    #ModalBoxProject .title{
        font-size: 1.7em;
        margin-top: 1rem;
    }
    #ModalBoxProject .info{
        margin: 0 12rem;
        margin-bottom: 2rem;
    }
}

