*{
  scroll-behavior: smooth;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
}

.leyd-h2{
  margin-bottom: 1.5em;
}

.badge{
  border-radius: 10px;
  color: #ffffff !important;
  margin: .2rem !important;
  float: none !important;
}

/* Mobile Devices <= 600px ".s" container 90% */
@media (max-width: 600px) { 
  .leyd-flex-s{
    display: flex;
  }
}

/* Mobile Devices > 600px ".m" container 85% */
@media (min-width: 601px) and (max-width: 992px) { 
  .leyd-flex-m{
    display: flex;
  }
}

/* Mobile Devices > 992px ".l" container 70% */
@media (min-width: 993px) and (max-width: 1200px) { 
  .leyd-flex-l{
    display: flex;
  }
}

/* Mobile Devices > 1200px ".xl" container 70% */
@media (min-width: 1201px) {
  .leyd-flex-xl{
    display: flex;
  }
}
