.leyd-header{
    width: 100%;
    height: 25rem;
    background-color: #212121;
    background-image: url(/assets/Untitled.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30%;
}
.leyd-container-filter{
    width: 100%;
    height: 100%;
    background-color: #2121219c;
    display: flex;
    align-items: center;
    justify-content: center;
}
.leyd-content-title-description{
    display: block;
    text-align: center;
}

.leyd-header-decription{
    color: #fff;
    font-size: 1.6rem;
    font-weight: lighter;
}

.leyd-logo-svg-header{
    width: 25em;
}

/* Mobile Devices <= 600px ".s" container 90% */
@media (max-width: 600px) { 
    .leyd-logo-svg-header{
        width: 20em;
    }
}

